.profile__user-name {
  max-width: 294px;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
  margin-right: 18px;
}

@media screen and (max-width: 740px) {
  .profile__user-name {
    font-size: 27px;
    line-height: 33px;
    max-width: 189px;
    margin-right: 0;
  }
}